import { Component, OnInit } from '@angular/core';
import { Form, FormBuilder, Validators, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { environment } from 'src/environments/environment';
import { CnDataService } from '../services/cn-data.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  loginForm:FormGroup;
  procesando:boolean = false;
  errorMessage: string = '';

  constructor(
    private router: Router,
    private fb: FormBuilder,
    private cnDataService: CnDataService,
  ) { 
    this._createForm();
  }

  ngOnInit() {
    this._createForm();
  }

  private _createForm(){
    this.loginForm = this.fb.group({
      username: [null, [Validators.required]],
      password: [null, [Validators.required]],
      perfil: [null, Validators.required]
    })
  }

  tryLogin(params){
    
    if(params['perfil'] === 'profesor'){
      this.errorMessage = "No tiene las opciones asociadas";
      return;
    }
    let usuarios = [];
    let encontrado = false;
    this.cnDataService.getCnData("usuarios")
      .then(res => {
        usuarios = res;
        usuarios.forEach(u => {
          console.log(u, params);
          if(params['username'] === u['username'] && params['password'] === u["password"] && params['perfil'] === u["perfil"]){
            let user = JSON.stringify(u);
            sessionStorage.setItem("USUARIO", user);
            encontrado = true;
            this.router.navigate([`${environment.defaultRoute}`]);
          return;
          }
        })
      });
      setTimeout(() => {
        if(!encontrado){
          this.errorMessage = "Credenciales incorrectas";
          return;
        } 
      }, 1500);
      
   
  }

}
