import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class ApiService {

  private host: string = environment.host;

  constructor(
    private http: HttpClient
  ) { }

  private get _getOptions() {
    return {
      headers: new HttpHeaders({
        'Content-Type':  'application/json',
   //     'Authorization': `Bearer ${localStorage.getItem('token') ? localStorage.getItem('token') : ''}`
      })
    }
  }

  public get(path: String) {
    return this.http.get(`${this.host}${path}`, this._getOptions)
      .pipe(
        map(res => { return res })
      )
  }

  public post(path: String, data: Object) {
    return this.http.post(`${this.host}${path}`, data, this._getOptions).pipe(
      map(res => { return res })
    );
  }

  public put(path: String, data: Object) {
    return this.http.put(`${this.host}${path}`, data, this._getOptions).pipe(
      map(res => { return res })
    );
  }


  public delete(path: String) {
    return this.http.delete(`${this.host}${path}`, this._getOptions).pipe(
      map(res => { return res })
    )
  }

}
