import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class CnDataService {

  private path: string = './assets/data/cn-data.json'

  constructor(private http: HttpClient) { }

  /**
   * Filtra por tabla
   * @param obj object
   * @param tableName string
   */
  private _filterTable(obj: object, tableName: string = "") {
    if (tableName) {
      for (let i = 0; i < Object.keys(obj).length; i++) {
        if (obj[i].table === tableName) {
          return obj[i].data;
        }
      }
    }
    return obj;
  }

  /**
   * Filtrar por key
   * @param obj object
   * @param key string
   * @param tableName string
   */
  private _filterData(obj: object, tableName: string = "", key: string = "") {
    let _obj = this._filterTable(obj, tableName);
    if (key) {
      for (let i = 0; i < Object.keys(_obj).length; i++) {
        if (_obj[i].id === key) {
          return _obj[i];
        }
      }
    }
    return _obj;
  }
  
 /**
   * Get data from local json
   * @param tableName departments, provinces, districs
   * @param key string
   */
  public getCnData(tableName:string, key: string = '') {
    return this.http.get(this.path).toPromise().then(res => {
      return this._filterData(res, tableName, key);
    }).catch(err => {
      console.log('__cnDataService.getCnData', err);
      return err;
    })
  }

}
