import { Injectable } from '@angular/core';
import { ApiService } from './api.service';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class EventoService {

  private _path : String = 'evento';

  constructor(private api: ApiService) { }

  public getPaginado(page?){
    return this.api.get(`${this._path}?page=${page ? page : 1}`).pipe(
      map(res => res)
    )
  }

  public getAll(page?){
    return this.api.get(`${this._path}/all`).pipe(
      map(res => res)
    )
  }

  public getEvento(id:number){
    return this.api.get(`${this._path}/${id}`).pipe(
      map(res => res)
    )
  }

  public create(data:object){
    return this.api.post(`${this._path}/create`, data).pipe(
      map(res => res)
    )
  }

  public update(person:object){
    return this.api.put(`${this._path}`, person).pipe(
      map(res => res)
    )
  }

  public delete(id:number){
    return this.api.delete(`${this._path}/${id}`).pipe(
      map(res => res)
    )
  }

  public matricular(idEvento:number, idProceso:number){
    let data = {
      "idEvento": idEvento,
      "idProceso": idProceso
    };
    return this.api.post(`${this._path}/persona`, data).pipe(
      map(res => res)
    );
  }

  public deleteGroup(ids:Array<Number>){
    return this.api.post(`${this._path}/delete`, ids).pipe(
      map(res => res)
    )
  }

  public cloneGroup(ids:Array<Number>){
    return this.api.post(`${this._path}/create/clone`, ids).pipe(
      map(res => res)
    )
  }

  public filter(data:object){
    return this.api.post(`${this._path}/find/criteria`, data).pipe(
      map(res => res)
    )
  }

}
